import { SiGithub} from 'react-icons/si';
import {AiOutlineLinkedin} from "react-icons/ai";
import React, {useEffect, useState} from 'react';
import '../App.css';
import {Navbar, Container, Nav, NavDropdown} from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { CSSTransition } from 'react-transition-group';

import BGClips from "../shortClips/FallingStars5Videvo.mov"

const Introducing = (props) =>{

    const handleIntro = (status) => {
        document.body.style.overflowX = "hidden";
        document.body.style.overflowY = "hidden";

        if(status){
        document.getElementsByClassName("changeME")[0].classList.remove("fade-out")
        document.getElementsByClassName("changeME")[0].classList.remove("fade-in")
        setTimeout(function() {
            document.getElementsByClassName("changeME")[0].classList.add("fade-out")
            
        }, 2000);

        setTimeout(function() {
            document.getElementsByClassName("changeME")[0].innerHTML = "I am Isa, A Software Developer :)"
            document.getElementsByClassName("changeME")[0].classList.remove("fade-out")
            document.getElementsByClassName("changeME")[0].classList.add("fade-in")
            document.body.style.overflowX = "hidden"
            document.body.style.overflowY = "visible"
            props.SetShowEverything(true)
        }, 4500);
    }
      }
    

    const style = {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100vh",
        width: "100vw",
        zIndex:"1"
        
    }


      


    return (
    <div style={props.ShowEverything?style:null}>
        <CSSTransition 
        in={props.ShowEverything}
        timeout={250}
        classNames="JustFading"
        unmountOnExit
        >
        <Navbar 
        collapseOnSelect
        expand="lg" 
        variant="dark" 
        style={
            {position: "absolute",
            top: "0",
            left: "0",
            width:"100vw",
            backgroundColor: "#343a4000!important"
        }
        }
        >
            <Container>
                <Nav className="me-auto">
                <a href="https://www.linkedin.com/in/isa-m-6055b6194" target="_blank">
                    <AiOutlineLinkedin/>
                </a>
                <a href="https://github.com/Isa0801" target="_blank">
                    <SiGithub/>
                </a>
                </Nav>
            </Container>
        </Navbar>
        </CSSTransition>
        <CSSTransition 
        in={!props.Starting}
        timeout={250}
        classNames="StartingPage"
        onEnter={()=>handleIntro(true)}
        unmountOnExit
        >
        <div style={{zIndex:"1"}}>
        <h1 className="changeME">Hello</h1>
        </div>
        </CSSTransition>

        <CSSTransition 
        in={!props.Starting}
        timeout={0}
        classNames="StartingPage"
        unmountOnExit
        >
                <video 
                autoPlay={true}
                loop={true}
                muted={true}
                controls={false}
                playsInline
                style={{
                    position: "fixed",
                    top: "50%",
                    left: "50%",
                    transform: "translate(-50%,-50%)",
                    height:"100%",
                    width:"100vw",
                    objectFit: "cover",
                    zIndex:"-1"
                }}
                
                >
                    <source src={BGClips} type="video/mp4"></source>
                </video>
        </CSSTransition>

    </div>
    )

}


export default Introducing;