import React from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import {Card, CardGroup, ListGroup, ListGroupItem} from "react-bootstrap";
import Abbjpg from "../icons/Abb.jpg";
import EpirocPng from "../icons/Epiroc.png";
import ResumeLogoSvg from "../icons/undraw_Resume_folder_re_e0bi.svg";
import '../App.css';
import { CSSTransition } from 'react-transition-group';

const Work = (props) =>{


    const workExperArr = [
        [Abbjpg, "Software Developer Intern", "As an intern, I got working with old VBA code and making C# variant to handle the same task and improve upon them, also had to automate some parts for the users."],
        [EpirocPng, "Software Developer Intern", "As an intern, I got assigned to testing out different compression libraries, in terms of compression speed and rate."]
            ]
    

    const style = {
        minHeight: "100vh",
        minWidth: "100vw",
        backgroundColor: "rgb(47 62 82 / 0%)",
        paddingTop: "50px",
        position: "relative",
        zIndex: "1"
        
    }

    const cardStyle = {
        padding: "15px",
        width: '30rem',
        maxWidth: '100vw',
        backgroundColor: "#001a3338",
        margin: "auto",
        marginTop: "15px",
        marginBottom: "15px",
        borderRadius: "5%",
        boxShadow: "0 0 10px 1px rgb(6 6 6 / 80%)"

    }

    

    return (
        
        <CSSTransition 
        in={props.ShowEverything}
        timeout={250}
        classNames="JustFading"
        unmountOnExit
        >
        <div style={style}>
        <h2>Work Experience</h2>
        <img style={{maxWidth:"100vw", width: "20rem"}} src={ResumeLogoSvg} alt="Image of figures, throwing graduation caps" />
        <div style={{display: "flex",flexWrap: "wrap"}}>
        {
        workExperArr.map((work, idx)=> (
            <Card style={cardStyle}>
            <Card.Img 
            style={{maxWidth: "50%",
                    margin: "auto",
                    borderRadius: "100%"}}
            variant="top"
            src={work[0]}
            alt="logo of Abb"
            />
                <Card.Body>
                    <Card.Title>{work[1]}</Card.Title>
                    <Card.Text>
                        {work[2]}
                    </Card.Text>
             </Card.Body>
            </Card>) )
        }
        </div>
        </div>
        </CSSTransition>

        
    )

}


export default Work;
