import React from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import {Card, CardGroup, ListGroup, ListGroupItem} from "react-bootstrap";
import educationSvg from "../icons/undraw_Graduation_re_gthn.svg";
import ecLogoSvg from "../icons/ec_logo.svg";
import '../App.css';
import { CSSTransition } from 'react-transition-group';

const Education = (props) =>{


    const EcSubjects = [
        "Project Knowledge & Working Methods",
        "C, C++",
        "Industrial IT Security",
        "Enterprise Manufacturing Intelligence (EMI)",
        "Industrial Cloud Solutions",
        "Advanced PLC Programming",
        "Industrial IT & Communications",
        "Programming Projects",
        "Integration & Monitoring - Internet Of Things"
            ]
    

    const style = {
        minHeight: "100vh",
        minWidth: "100vw",
        backgroundColor: "rgb(47 62 82 / 0%)",
        paddingTop: "50px",
        position: "relative",
        zIndex: "1"
        
    }

    const cardStyle = {
        padding: "15px",
        width: '30rem',
        maxWidth: '100vw',
        backgroundColor: "#001a3338",
        margin: "auto",
        marginTop: "15px",
        marginBottom: "15px",
        borderRadius: "5%",
        boxShadow: "0 0 10px 1px rgb(6 6 6 / 80%)"

    }

    

    return (
        
        <CSSTransition 
        in={props.ShowEverything}
        timeout={250}
        classNames="JustFading"
        unmountOnExit
        >
        <div style={style}>
        <h2>Education</h2>
        <img style={{maxWidth:"100vw"}} src={educationSvg} alt="Image of figures, throwing graduation caps" />

        <Card style={cardStyle}>
            <Card.Img variant="top" src={ecLogoSvg} alt="logo of ec utbildning" />
            <Card.Body>
                <Card.Title>Software Development Within Industrial IOT</Card.Title>
                <Card.Text>
                I learned a lot about programming as well as how to combine microcontrollers with cloud-based solutions.
                </Card.Text>
            </Card.Body>
            <Card.Title>Subjects</Card.Title>
            <ListGroup>
                {EcSubjects.map((sub,idx) => (
                    <ListGroupItem>{sub}</ListGroupItem>
                ))}
            </ListGroup>
        </Card>
        </div>
        </CSSTransition>

        
    )

}


export default Education;
