import { BsArrowLeft, BsArrowRight } from 'react-icons/bs';
import React from 'react';
import '../App.css';
import { CSSTransition } from 'react-transition-group';

const StartingPage = (props) =>{

    const handleStarterClickl = () => {
        document.getElementsByClassName("svgStarterLeft")[0].classList.add("svgStarterLeftAtive")
        document.getElementsByClassName("svgStarterRight")[0].classList.add("svgStarterRightAtive")
        setTimeout(function() {props.SetStarting(false);
    
        }, 500);
      }


    return (
        <CSSTransition 
        in={props.Starting}
        timeout={250}
        classNames="StartingPage"
        unmountOnExit
        >
    <div className="starterBtn" onClick={handleStarterClickl}>
    <BsArrowLeft className="svgStarter svgStarterLeft"/>
    <BsArrowRight className="svgStarter svgStarterRight"/>
    </div>
    </CSSTransition>)

}


export default StartingPage;