import { SiGithub} from 'react-icons/si';
import {AiOutlineLinkedin} from "react-icons/ai";
import coderSvg from "../icons/Web-Developer-Flatline.svg";
import React, {useEffect, useState} from 'react';
import {Card, CardGroup} from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import '../App.css';
import { CSSTransition } from 'react-transition-group';

const SkillSets = (props) =>{
    

    const style = {
        minWidth: "100vw",
        backgroundColor: "#001a3338",
        paddingTop: "50px",
        position: "relative",
        zIndex: "1"
        
    }

    const arrr = [
        ['C++ & C', "Mostly Confident", "I have used C++ and C during my education and 3 months internship at Epiroc."],
        ['C#', "Slightly Confident", "I have used C# during in my free time and 3 months internship at Hitachi Abb."],
        ['HTML, CSS, JS & REACT', "Less Confident", "I have been learning web dev in free time, just for fun."],
        ['PYTHON', "Less Confident", "I used Python to make simple project/scripts."]
        ]
      


    return (
        
        <CSSTransition 
        in={props.ShowEverything}
        timeout={250}
        classNames="JustFading"
        unmountOnExit
        >
        <div style={style}>
            <h2>Skill Sets</h2>
        <img src={coderSvg} alt="Image of person coding" />
        <div style={{width:"95%", margin: "auto"}}>
        <CardGroup>
        {
        arrr.map((variant, idx) => (
        <Card
            key={idx}
            text={'white'}
            className="mb-2"
            style={{    backgroundColor: "rgb(0 0 0 / 40%)",
                borderRadius: "5%",
                margin: "5px"}}
        >
            <Card.Header>{variant[0]}</Card.Header>
            <Card.Body>
            <Card.Title>{variant[1]}</Card.Title>
            <Card.Text style={{fontSize:"large"}}>
                {variant[2]}
            </Card.Text>
            </Card.Body>
        </Card>
        ))
    }
            </CardGroup>
        </div>
        </div>
        </CSSTransition>

        
    )

}


export default SkillSets;